import React, {Component} from 'react';
import Chart from "chart.js";
import Dados from '../../dados.js';

export default class Mortes extends Component{

  state = {
    data: "",
    curados: 0
  }

  curados = React.createRef();
  componentDidMount() {

    const data = Dados()[0].data[Dados()[0].data.length-1];
    const curados = Dados()[0].curados[Dados()[0].curados.length-1];

    this.setState({data, curados});

    const myChartRef = this.curados.current.getContext("2d");
    var myChart = new Chart(myChartRef, {
    type: 'bar',
    data: {
        labels: Dados()[0].data,
        datasets: [{
            label: 'Mortes',
            data: Dados()[0].curados,
            backgroundColor: 'rgba(3, 138, 32, .5)',
            borderColor: 'rgba(3, 138, 32, 1)',
            borderWidth: 3,
            pointRadius: 0
        }]
    },
    options: {
        legend: {
          display: false
        }
    }
});
  }

  render(){
    const {data, curados} = this.state;
    return(
      <div>
        <span className="card-description mb-5">{data} - <strong>{curados} pessoas curadas</strong></span>
        <div className="card-body">
          <div className="curados">
            <canvas ref={this.curados} height={window.innerWidth > 725? 80 : 150}/>
          </div>
        </div>
      </div>
    )
  }
}
