import React, {Component} from 'react';
import Chart from "chart.js";
import Dados from '../../dados.js';

export default class Enfermaria extends Component{
  enfermaria = React.createRef();
  componentDidMount() {

    let aux = Dados()[0].ocupationEnfermaria.length-1
    let dado1 = Dados()[0].ocupationEnfermaria[aux];
    let dado2 = 100-dado1;

      const myChartRef = this.enfermaria.current.getContext("2d");
      var myChart = new Chart(myChartRef, {
    type: 'doughnut',
    data: {
        labels: ['ocupado', 'desocupado'],
        datasets: [{
            label: 'em tratamento ',
            data: [dado1,dado2],
            backgroundColor: ['rgba(255, 235, 59, 0.5)','rgba(0, 255, 255, 0)'],
            borderColor: ['rgba(255, 235, 59, 1)','rgba(0, 0, 0, .3)'],
            borderWidth: 3,
            pointRadius: 0
        }]
    },
    options: {
        legend: {
          display: false
        },
        scales: {
            yAxes: [{
              gridLines: false,
              ticks: {
                  display: false //this will remove only the label
              }
            }],
            xAxes: [{
              gridLines: false,
              ticks: {
                    display: false //this will remove only the label
                }
            }]
        }
    }
});
  }

  render(){
    return(
      <div className="enfermaria">
        <canvas ref={this.enfermaria} height={150}/>
      </div>
    )
  }
}
