import React, {Component} from 'react';
import Chart from "chart.js";
import Dados from '../../dados.js';

export default class Uti extends Component{
  uti = React.createRef();
  componentDidMount() {

      let aux = Dados()[0].ocupationUti.length-1
      let dado1 = Dados()[0].ocupationUti[aux];
      let dado2 = 100-dado1;

      const myChartRef = this.uti.current.getContext("2d");
      var myChart = new Chart(myChartRef, {
    type: 'doughnut',
    data: {
        labels: ['ocupado', 'desocupado'],
        datasets: [{
            data: [dado1,dado2],
            backgroundColor: ['rgba(255, 0, 0, 0.5)','rgba(0, 255, 255, 0)'],
            borderColor: ['rgba(255, 0, 0, 1)','rgba(0, 0, 0, .3)'],
            borderWidth: 3,
            pointRadius: 0
        }]
    },
    options: {
        legend: {
          display: false
        },
        scales: {
            yAxes: [{
              gridLines: false,
              ticks: {
                  display: false //this will remove only the label
              }
            }],
            xAxes: [{
              gridLines: false,
              ticks: {
                    display: false //this will remove only the label
                }
            }]
        }
    }
});
  }

  render(){
    return(
      <div className="uti">
        <canvas ref={this.uti} height={150}/>
      </div>
    )
  }
}
