import React, {Component} from 'react';
import Chart from "chart.js";
import Dados from '../../dados.js';

export default class Mortes extends Component{

  state = {
    data: "",
    Mortes: 0
  }

  mortes = React.createRef();
  componentDidMount() {

    const data = Dados()[0].data[Dados()[0].data.length-1];
    const mortes = Dados()[0].mortes[Dados()[0].mortes.length-1];

    this.setState({data, mortes});

    const myChartRef = this.mortes.current.getContext("2d");
    var myChart = new Chart(myChartRef, {
    type: 'bar',
    data: {
        labels: Dados()[0].data,
        datasets: [{
            label: 'Mortes',
            data: Dados()[0].mortes,
            backgroundColor: 'rgba(156, 39, 39, .5)',
            borderColor: 'rgba(156, 39, 39, 1)',
            borderWidth: 3,
            pointRadius: 0
        }]
    },
    options: {
        legend: {
          display: false
        }
    }
});
  }

  render(){
    const {data, mortes} = this.state;
    return(
      <div>
        <span className="card-description mb-5">{data} - <strong>{mortes} mortes registradas</strong></span>
        <div className="card-body">
          <div className="mortes">
            <canvas ref={this.mortes} height={window.innerWidth > 725? 120 : 150}/>
          </div>
        </div>
      </div>
    )
  }
}
