import React, {Component} from 'react';
import Chart from "chart.js";
import Dados from '../../dados.js';

export default class NovosCasos extends Component{

  state = {
    data: "",
    novosCasos: 0
  }

  novos = React.createRef();
  componentDidMount() {

    const data = Dados()[0].data[Dados()[0].data.length-1];
    const novosCasos = Dados()[0].novosCasos[Dados()[0].novosCasos.length-1];

    this.setState({data, novosCasos});

    const myChartRef = this.novos.current.getContext("2d");
    var myChart = new Chart(myChartRef, {
    type: 'bar',
    data: {
        labels: Dados()[0].data,
        datasets: [{
            label: 'Mortes',
            data: Dados()[0].novosCasos,
            backgroundColor: 'rgba(156, 39, 39, .5)',
            borderColor: 'rgba(156, 39, 39, 1)',
            borderWidth: 3,
            pointRadius: 0
        }]
    },
    options: {
        legend: {
          display: false
        }
    }
});
  }

  render(){
    const {data, novosCasos} = this.state;
    return(
      <div>
        <span className="card-description mb-5">{data} - <strong>{novosCasos} novos casos registrados</strong></span>
        <div className="card-body">
          <div className="novos">
            <canvas ref={this.novos} height={window.innerWidth > 725? 120 : 150}/>
          </div>
        </div>
      </div>
    )
  }
}
