import React, {Component} from 'react';
import './style.css';
import Dados from '../../dados.js';

export default class Media extends Component{

  state = {
    total: 0
  }

  componentDidMount(){
    let novosCasos = 0;
    let casos = Dados()[0].tratamento.length;
    for (var i = casos-1; i >= casos-30; i--) {
      novosCasos += Dados()[0].novosCasos[i];
    }
    let total = (novosCasos/=30).toFixed(0);
    this.setState({total: total})
  }

  render(){
    const {total} = this.state;
    return(
      <div className="media center">
        <span>{total}</span>
      </div>
    )
  }
}
