import React, {Component} from 'react';
import Chart from "chart.js";
import Dados from '../../dados.js';


export default class Letalidade extends Component{
  letalidade = React.createRef();
  componentDidMount() {


    let auxMortes = 0
    let auxCasos = 4
    for (var i = 0; i < Dados()[0].mortes.length; i++) {
      auxMortes += Dados()[0].mortes[i]
    }
    for (var i = 0; i < Dados()[0].mortes.length; i++) {
      auxCasos += Dados()[0].novosCasos[i]
    }
    let dado1 = (auxMortes*100)/auxCasos
    dado1 = dado1.toFixed(2);

    let dado2 = 100-dado1;

      const myChartRef = this.letalidade.current.getContext("2d");
      var myChart = new Chart(myChartRef, {
    type: 'doughnut',
    data: {
        labels: ['mortes', 'sobrevivencia'],
        datasets: [{
            label: 'em tratamento ',
            data: [dado1, dado2],
            backgroundColor: ['rgba(255, 87, 34, 0.5)','rgba(0, 255, 255, 0)'],
            borderColor: ['rgba(255, 87, 34, 1)','rgba(0, 0, 0, .3)'],
            borderWidth: 3,
            pointRadius: 0
        }]
    },
    options: {
        legend: {
          display: false
        },
        scales: {
            yAxes: [{
              gridLines: false,
              ticks: {
                  display: false //this will remove only the label
              }
            }],
            xAxes: [{
              gridLines: false,
              ticks: {
                    display: false //this will remove only the label
                }
            }]
        }
    }
});
  }

  render(){
    return(
      <div className="letalidade">
        <canvas ref={this.letalidade} height={150}/>
      </div>
    )
  }
}
