import React, {Component} from 'react';
import Chart from "chart.js";
import Dados from '../../dados.js';

export default class Curados extends Component{

  state = {
    data: "",
    resultado: 0
  }

  aguardandoResultado = React.createRef();
  componentDidMount() {

    const data = Dados()[0].data[Dados()[0].data.length-1];
    const resultado = Dados()[0].resultado[Dados()[0].resultado.length-1];

    this.setState({data, resultado});

    const myChartRef = this.aguardandoResultado.current.getContext("2d");
    var myChart = new Chart(myChartRef, {
    type: 'bar',
    data: {
        labels: Dados()[0].data,
        datasets: [{
            label: 'Mortes',
            data: Dados()[0].resultado,
            backgroundColor: 'rgba(156, 39, 39, .5)',
            borderColor: 'rgba(156, 39, 39, 1)',
            borderWidth: 3,
            pointRadius: 0
        }]
    },
    options: {
        legend: {
          display: false
        }
    }
});
  }

  render(){
    const {data, resultado} = this.state;
    return(
      <div>
        <span className="card-description mb-5">{data} - <strong>{resultado} pessoas aguardando os resultados</strong></span>
        <div className="card-body">
          <div className="aguardandoResultado">
            <canvas ref={this.aguardandoResultado} height={window.innerWidth > 725? 120 : 150}/>
          </div>
        </div>
      </div>
    )
  }
}
