import React, {Component} from 'react';
import Chart from "chart.js";
import Dados from '../../dados.js';

export default class Tratamento extends Component{

  state = {
    data: "",
    casos: 0
  }

  tratamento = React.createRef();
  componentDidMount() {

    const data = Dados()[0].data[Dados()[0].data.length-1];
    const casos = Dados()[0].tratamento[Dados()[0].tratamento.length-1];

    this.setState({data, casos});

    const myChartRef = this.tratamento.current.getContext("2d");
    var myChart = new Chart(myChartRef, {
    type: 'bar',
    data: {
        labels: Dados()[0].data,
        datasets: [{
            label: 'em tratamento ',
            data: Dados()[0].tratamento,
            backgroundColor: 'rgba(156, 39, 176, .5)',
            borderColor: 'rgba(100, 0, 100, 1)',
            borderWidth: 1,
            pointRadius: 2
        }]
    },
    options: {
        legend: {
          display: false
        }
    }
});
  }

  render(){
    const {data, casos} = this.state;
    return(
      <div>
        <span className="card-description mb-5">{data} - <strong>{casos} pessoas em tratamento</strong></span>
        <div className="card-body">
          <div className="tratamento">
            <canvas ref={this.tratamento} height={window.innerWidth > 725? 80 : 150}/>
          </div>
        </div>
      </div>
    )
  }
}
